:root {
    --dblue: #1b4058;
    --lblue: #C1DBEC;
    --white: #edf2f4;
    --orange: #f58f29;
}

body::-webkit-scrollbar {
    display: none; /*Chrome, Safari, Opera*/
}

body {
    background: linear-gradient(to right, #0f0c29, #302b63, #24243e);
    overflow-y: scroll;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    overflow-x: hidden;
}

.whitetext {
    color: var(--white);
}

header {
    font-size: 40px;
    font-family: 'Audiowide';
    color: var(--white);
}

h1 {
    font-size: 40px;
    font-family: 'Audiowide';
    color: var(--white);
}

h2 {
    font-size: 30px;
    font-family: 'Kelly Slab';
    color: var(--white);
}

p {
    font-family: 'Sanchez';
    color: var(--white);
    font-size: 20px;
}

#aboutmediv {
    margin: 100px;
    text-align: center;
}

#startingtitle {
    font-family: 'Audiowide', 'Times New Roman', Times, serif;
    position: relative;
    text-align: center;
    top: 250px;
    color: var(--white);
}

#startingdiv {
    height: 800px;
    margin: -50px -8px;
    top: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttons {
    position: relative;
    top: -20px;
}

    .buttons a {
        display: inline-block;
        height: 10px;
        width: 10px;
        border-radius: 50px;
        background-color: var(--white);
        margin: -50px -8px;
        top: 50%;
    }

#ablinks a {
    border: 2px solid transparent;
    border-radius: 3px;
    font-family: Audiowide;
    text-decoration: none;
    color: var(--white);
    transition: ease .3s;
    display: inline;
    float: right;
    margin: 5px;
    margin-top: 13px;
}

    #ablinks a:hover {
        border-bottom: 2px solid var(--orange);
    }

.Name {
    float: left;
}

.quote {
    color: var(--orange)
}

#meface {
    border-radius: 50%;
    margin: auto;
    height: 250px;
}

#expdiv {
    display: flex;
    justify-content: center;
    text-align: center;
}

#exptable {
    width: 600px;
    border-spacing: 20px 5px;
}

.kellyslab {
    font-family: 'Kelly Slab', Arial, Helvetica, sans-serif;
    color: var(--white);
}

td {
    color: inherit;
    font-family: 'Sanchez';
    font-size: 18px;
    vertical-align: top;
}

th {
    color: inherit;
    font-family: 'Kelly Slab';
    font-size: 25px;
    vertical-align: top;
}

#employers td a {
    text-decoration: none;
    color: var(--white);
    border: 2px solid transparent;
    border-radius: 3px;
    transition: ease .3s;
}

    #employers td a:hover {
        border-bottom: 2px solid var(--orange);
    }

a {
    color: var(--white);
    text-decoration: none;
}

#projectstable {
    color: var(--white);
    display: flex;
    justify-content: center;
    text-align: center;
}

.projectsimg {
    height: 8em;
    width: 15em;
}

.projects-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.project {
    width: 20%;
    height: 20%;
    margin-bottom: 2rem;
    margin-left: 10px;
    margin-right: 10px;
    padding: .5rem;
    background-color: white;
    border-radius: 25px;
    transition: width .5s;
}

    .project img {
        width: 100%;
        height: auto;
    }

    .project p {
        margin-top: 1rem;
        font-size: 0.9rem;
    }

#projectHeader {
    display: flex;
    justify-content: center;
}

.project h2 {
    color: black;
    display: flex;
    justify-content: center;
    font-size: 190%;
    transition: font-size .5s;
}

.project p {
    color: black;
    display: flex;
    justify-content: center;
}

.projects-container a {
    display: contents;
    justify-content: center;
}

.project:hover {
    width: 22%;
    font-size: 105%;
}

.dropdown-content {
    display: none;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    z-index: 1;
    transition: visibility .5s, opacity .5s;
}

    .dropdown-content.show {
        background-color: whitesmoke;
        width: 110px;
        display: block;
        position: absolute;
        visibility: visible;
        opacity: 100;
        top: 52px;
        right: 0px;
        padding-right: 2px;
        justify-content: flex-end;
        transition: visibility .5s, opacity .5s linear;
        border-bottom-left-radius: 15px;
    }

    .dropdown-content a {
        color: black !important;
    }
